





































































import { Component, Vue } from 'vue-property-decorator';
// import moment from 'moment';

class Project{
  public items: object[] = [];
  public id !: symbol;
  public title = "";
  public proj_search = "";
  public projects = [];
  public tempTitle = "new project"

  constructor(title = '', items = []){
    this.title = title;
    this.id = Symbol();

    if(items.length){
      this.items = items;
    }
  }

  public setTitle(title: string){
    this.title = title;
  }

  public createItem(text: string, date: object){
    const item = {
      txt: text,
      done: false,
      date: date || new Date()
    }

    this.items.push(item);
  }
}

@Component({
})
export default class App extends Vue {
  private inp = "";
  private current = 0;
  private projects: any[] = [];
  private projSearch = "";
  private titleInp = "";
  private itemInp = "";

  get searchResult(){
    return this.projects.filter((item) => {
      return this.projSearch.toLowerCase().split(' ').every(i => item.title.toLowerCase().includes(i));
    })
  }

  get projectsWithTitle(){
    return this.projects.some(i => i.title.length)
  }

  private mounted(){
    if(localStorage.getItem('projects')){
      this.load();
    }
  }

  public addProjectItem(){
    if(this.itemInp){
      this.projects[this.current].createItem(this.itemInp);
      this.itemInp = '';
    }
  }

  public clear(){
    this.titleInp = "";
    this.itemInp = "";
    this.projects = [];
    localStorage.clear();
  }

  public load(){
    this.current = 0;
    const savedProjects = JSON.parse(localStorage.getItem('projects') || '{}');
    for(const proj of savedProjects){
      this.projects.push(new Project(proj.title, proj.items));
    }
  }

  public save(){
    if(this.projectsWithTitle){
      localStorage.setItem('projects', JSON.stringify(this.projects));
    }
  }

  public setProjectTitle(){
    this.projects[this.current].setTitle(this.titleInp);
    this.titleInp = '';
  }

  public addProject(){
    this.projSearch = "";
    const project = new Project();

    this.projects.push(project);
    this.current = this.projects.length-1;
  }
}
